    "Вопросы": "Освобождение потоков",
    "Ответы": "Disposable Является связующим звеном между Observable и active Observer, вызов его dispose()метода останавливает выбросы и удаляет все ресурсы, используемые для этого Observer:\n fun main() {\n  val seconds = Observable.interval(1, TimeUnit.SECONDS)\n  val disposable = seconds.subscribe { println(\"Received: $it\") }\n  TimeUnit.SECONDS.sleep(5)\n  \/\/ Dispose and stop emissions.\n  disposable.dispose()\n  \/\/ Sleep 5 secs: no new emissions.\n  TimeUnit.SECONDS.sleep(5)\n }\n Received: 0\n Received: 1\n Received: 2\n Received: 3\n Received: 4"
