    "Вопросы": "MapReduce это? Пример",
    "Ответы": "MapReduce – это модель программирования для написания приложений, которые могут обрабатывать большие данные параллельно на нескольких узлах. MapReduce предоставляет аналитические возможности для анализа огромных объемов сложных данных.\n \n Пример\n Предположим, у нас есть данные о сотрудниках в четырех разных файлах (имя, зп).\n \n Фаза Map обрабатывает каждый входной файл и предоставляет данные о сотруднике в парах ключ-значение (<k, v>: <emp name, salary>)\n Фаза объединителя (метод поиска) будет принимать входные данные из фазы карты в виде пары ключ-значение с именем сотрудника и зарплатой. Используя технику поиска, комбинатор проверит всю зарплату сотрудника, чтобы найти сотрудника с наибольшим окладом в каждом файле.\n \n <k: employee name, v: salary>\n Max= the salary of an first employee. Treated as max salary\n if(v(second employee).salary > Max){\n  Max = v(salary);\n }\n else{\n  Continue checking;\n }\n Ожидаемый результат: наибольшая зарплата сотрудника в каждом файле\n \n Этап сокращения – Сформируйте каждый файл, вы найдете самый высокооплачиваемый сотрудник. Чтобы избежать избыточности, проверьте все пары <k, v> и удалите дублирующиеся записи, если таковые имеются."
