    "Ответы": "Класс Mapper определяет задание Map. Сопоставляет входные пары ключ-значение с набором промежуточных пар ключ-значение. Карты – это отдельные задачи, которые преобразуют входные записи в промежуточные записи. Преобразованные промежуточные записи не обязательно должны быть того же типа, что и входные записи. Заданная входная пара может отображаться на ноль или на множество выходных пар. метод карта является наиболее известным методом класса Mapper. Синтаксис представлен map(KEYIN key, VALUEIN value, org.apache.hadoop.mapreduce.Mapper.Context context) Этот метод вызывается один раз для каждой пары ключ-значение во входном разбиении.",
