    "Вопросы": "Как работает Combiner?Что такое Combiner?Реализация",
    "Ответы": "Краткое описание того, как работает MapReduce Combiner: \n · У объединителя нет предопределенного интерфейса, и он должен реализовывать метод Redu () интерфейса Reducer. \n · Комбайнер работает с каждым ключом вывода карты. Он должен иметь те же типы значений выходного ключа, что и класс Reducer. \n · Объединитель может создавать сводную информацию из большого набора данных, поскольку он заменяет исходный вывод карты. \n Хотя Combiner не является обязательным, он помогает разделить данные на несколько групп для фазы сокращения, что упрощает обработку.\n \n Combiner, также известный как полуредуктор, является необязательным классом, который\n работает, принимая входные данные из класса Map и затем передавая выходные пары ключзначение в класс Reducer.\n Основная функция Combiner состоит в том, чтобы суммировать выходные записи карты с одним\n и тем же ключом. Выход (сбор значения ключа) объединителя будет отправлен по сети\n фактической задаче «Редуктор» в качестве входных данных.\n Класс Combiner используется между классом Map и классом Reduce для уменьшения объема\n передачи данных между Map и Reduce \n \n public static class IntSumReducer extends Reducer<Text,IntWritable,Text,IntWritable>\n {\n  private IntWritable result = new IntWritable();\n \n  public void reduce(Text key, Iterable<IntWritable> values,Context context) throws IOException, InterruptedEx\n ception\n  {\n  int sum = 0;\n  for (IntWritable val : values)\n  {\n  sum += val.get();\n  }\n  result.set(sum);\n  context.write(key, result);\n  }\n }",
