    "Ответы": "Атака Сивиллы (англ. Sybil attack) – это угроза безопасности, которая подразумевает контроль одним человеком или группой лиц над множеством сетевых узлов (нод) в одноранговой сети (P2P). Такую схему применяют для захвата контроля над сетью для ее использования в собственных целях, например, для фальсификации данных.\nВ контексте рынка криптовалют атаки Сивиллы работают похожим образом. Мошенники создают множество нод и подключают их к сети криптовалюты. Технически, сетевые узлы будут выглядеть независимыми. При этом управлять ими может один человек. Подконтрольные мошеннику ноды способны склонять другие сетевые узлы к одобрению искаженных данных."
