    "Вопросы": "Работа с backpressure в Reactor",
    "Ответы": "Backpressure (back pressure) – это то, с чем рано или поздно сталкивается почти любой разработчик, а для некоторых это становится частой и серьезной проблемой. В мире программирования термин “backpressure” является аналогией, позаимствованной из физики. В двух словах, backpressure – это сопротивление или некоторая сила, действующая в направлении, противоположном желаемому направлению движению частиц в трубе. В контексте программирования эту фразу можно переиначить: …это сопротивление желаемому потоку данных приложения. Цель любого приложения – это получение входных данных и приведение их к некоторому желаемому виду. Когда возникает backpressure, между входом и выходом возникает некое сопротивление. В большинстве случаев, узкое место возникает из-за ограничений в вычислительных мощностях – данные не успевают обрабатываться с всё нарастающей скоростью их поступления. Но есть и другие формы backpressure, например, когда приложение ожидает реакции пользователя на некоторое событие, которая, по понятным причинам, может запаздывать. Иногда под backpressure понимается не само явление как таковое, а конкретный механизм его обработки. Пример чтения и записи файлов Запись файла процесс более медленный, чем чтение. Если комбинация ОС, жесткого диска и библиотек обеспечивает эффективную скорость чтения в 150 мб\/с, а записи – в 100 мб\/с, то при чтении файла с последующей его записью обратно на диск, вы должны записывать в буфер “лишние” 50 мб каждую секунду. Решение очевидно: читать ровно столько, сколько сможете записать. Практически все библиотеки имеют соответствующие абстракции для работы с этими случаями. Взаимодействие между серверами Микросервисная архитектура, где каждый микросервис может разворачиваться на отдельном сервере, пользуется все большей популярностью. Backpressure в этом случае возникает, когда один сервер отправляет запросы другому слишком быстро и второй сервер не успевает их обрабатывать. Предположим, что Сервер A отправляет Серверу B 100 запросов в секунду, но Сервер B может обрабатывать только 75 из них. Имеем дефицит в 25 запросов в секунду. В любом случае, Сервер B должен каким-то образом работать с backpressure. Один из вариантов – это буферизация избыточных запросов, но если они будут сыпаться с прежней скоростью, однажды свободная память сервера закончится. Можно игнорировать избыточные запросы, но во многих случаях это запрещается требованиями к системе. Идеальный случай, когда Сервер B может сам контролировать поток запросов от Сервера А, но и это не всегда возможно. Например, если Сервер А генерирует запросы от имени пользователя. Мы же не можем сказать пользователям “притормозите!” (хотя иногда стОит!).",
