    "Вопросы": "Модель потоков Spring WebFlux",
    "Ответы": "На WebFlux сервере можно увидеть следующие потоки: На оригинальном сервере Spring WebFlux существует один поток для работы сервера и несколько потоков для обработки запросов (обычно их количество равно числу ядер в процессоре). Реактивный WebClient работает в режиме event loop. Reactor обеспечивает абстракции пула потоков, которые называются scheduler’ами. Для переключения обработки на другой пул используется метод publishOn(). Планировщики (scheduler’ы) названы в соответствии со стратегией работы, например parallel для CPUориентированной параллельной обработки с ограниченным количеством потоков, elastic для I\/O – с большим количеством потоков. Под капотом WebFlux трудится Reactor-Netty – обёртка над Netty с поддержкой backpressure. ReactorNetty создаёт Runtime.getRuntime().availableProcessors() * 2 потоков для использования во внутреннем EventLoopThreadPool. Если входящий запрос завершается блокирующим удалённым вызовом, то его нужно соответствующим образом обернуть, чтобы текущий поток мог вернуться в event-queue пул для обработки новых запросов. Для тяжёлых вычислительных операций имеет смысл использовать выделенный пул потоков. Это гарантирует, что EventLoop Netty будет только принимать и отправлять данные по сети, не тратя ресурсы на другие операции: @PostMapping public Mono cpuIntensiveProcessingHandler( Mono monoInput ) { return monoInput .publishOn(Schedulers.fromExecutorService(myOwnDedicatedExecutor)) .map(i -> doCpuIntensiveInImperativeStyle(i)); } Если интересен ручной контроль количества запросов, можно посмотреть в сторону метода limiRate(int n)",
