    "Вопросы": "Design for failure для распределенной системы",
    "Ответы": "Одно из наиболее критичных мест в микросервисной архитектуре — необходимость разрабатывать код для распределенной системы, составные элементы которой взаимодействуют через сеть. А сеть ненадежна по своей природе: сеть может просто отказать, работать плохо или вдруг перестать пропускать какой-то тип сообщений. Поэтому микросервисы могут перестать отвечать или начать отвечать медленнее. И каждый удаленный вызов должен это учитывать, должен правильно обрабатывать разные варианты отказа, уметь ждать, уметь возвращаться к нормальной работе при восстановлении контрагента. Дополнительный уровень сложности привносит событийная архитектура, как и отладка такой системы. И поскольку сложность таких систем очень высока, то проблему решают так: \n- Не доводят систему до идеального состояния. Это очень дорого. Система отвечает необходимым нефункциональным требованиям, но в ней могут присутствовать ошибки, незначительно влияющие на ее устойчивость и производительность. \n- С другой стороны вкладываются в инфраструктуру, которая помогает быстрее устранять нештатные ситуации. Должно быть полное покрытие кода unit тестами, интеграционными и тестами производительности. Должен быть интеллектуальный мониторинг, который не только моментально показывает неработающие места, но и сигнализирует об ухудшении состояния системы с прогнозированием возможных сбоев. Должно быть продвинутое распределенное логирование, позволяющее оперативно проводить расследования.",
