    "Вопросы": "Монолит против микросервисов, плюсы и минусы",
    "Ответы": "Микросервисный подход несет довольно много проблем. Например, организационные вопросы. Поэтому начинать путь к микросервисной архитектуре стоит с внедрения DevOps и Agile. Кроме организационных есть и чисто архитектурные. Как перейти от монолита, где всё синхронно, согласованно и едино, к распределенной событийной архитектуры, основанной на множестве мелких элементов, в которой надо учитывать возможную неконсистентность данных? Если у вас нет проблем с вашим «монолитом», то не надо их искать. Но если проблемы есть, то посмотрите на плюсы MSA, и возможно она спасет вас.\nРазбиение на независимые компоненты даёт безусловные и неоспоримые преимущества: легкое понимание контекста, гибкость развития, управления и масштабирования. Независимость и небольшой размер дают и неожиданные плюсы с точки зрения инфраструктуры. Микросервисы можно устанавливать на обычные дешевые машинки. И окажется, что даже все вместе они будут стоить на порядок меньше, но работать эффективнее той самой супермашины."
